@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*===== GOOGLE FONTS =====*/
/*===== VARIABLES CSS =====*/
:root {
  --header-height: 3rem;
  --nav-width: 68px;
  /*===== Colors =====*/
  --first-color: #4723d9;
  --first-color-light: #afa5d9;
  --white-color: #f7f6fb;

  /*===== Font and typography =====*/
  --body-font: "Nunito", sans-serif;
  --normal-font-size: 1rem;

  /*===== z index =====*/
  --z-fixed: 100;
}
/*===== BASE =====*/
*,
::before,
::after {
  box-sizing: border-box;
}

body {
  position: relative;
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  padding: 0 1rem;
  font-family: "Nunito", sans-serif;
  font-family: var(--body-font);
  font-size: 1rem;
  font-size: var(--normal-font-size);
  transition: 0.5s;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.button,
button {
  cursor: pointer;
  border: none;
  outline: none;
  padding: 5px 15px;
  border-radius: 15px;
}
.button__sp {
  cursor: pointer;
  border: none;
  outline: none;
  padding: 5px 15px !important;
}
.button:hover,
button:hover {
  opacity: 0.6;
}
a {
  text-decoration: none;
  color: #ffffff;
}
.add__button {
  background: linear-gradient(to right, #4723d9, dodgerblue);
  background: linear-gradient(to right, var(--first-color), dodgerblue);
  color: #ffffff;
  margin-top: 5px;
}

.white {
  color: white;
}
.dodgerblue {
  color: dodgerblue;
}
.purple {
  color: purple;
}
.green {
  color: green;
}
.red {
  color: red;
}

.bg-dodgerblue {
  background-color: dodgerblue;
}
.bg-green {
  background-color: green;
}
.bg-red {
  background-color: red;
}
.bd {
  font-weight: 700;
}
.ud {
  text-decoration: underline;
}
.tc {
  text-align: center;
}
.tl {
  text-align: left;
}

/* styles for dialog components */
.dialog {
  padding: 15px 20px;
}
.dialog > h3 {
  color: dodgerblue;
  text-align: center;
  text-decoration: underline;
}
.dialog textarea {
  min-width: 100%;
  width: 100%;
  max-width: 600px;
  min-height: 80px;
  max-height: 80px;
  border-radius: 15px;
  border: 2px solid lightgray;
  outline: none;
  padding: 5px 15px;
  resize: none;
}
.dialog textarea:focus {
  border: 2px solid dodgerblue;
}

/* for items spanning the entire width */
.dialog__rowSingleItem {
  display: flex;
  flex-direction: column;
  max-width: 95%;
  margin: 5px auto 10px auto;
}
.dialog__rowSingleItem > label {
  color: purple;
  font-weight: bold;
  padding: 3px 8px;
  width: 100%;
  max-width: 100%;
  margin: 2px auto;
}
.dialog__row {
  display: flex;
  margin: 10px auto;
  max-width: 95%;
}
.dialog__row > span {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 0.5 1;
  margin-right: 15px;
}
.dialog__row > span:nth-child(1) {
  margin-right: 20px;
}
.dialog__row label {
  color: purple;
  font-weight: bold;
  padding: 3px 8px;
}

.dialog__row input,
.dialog__row select,
.dialog__rowSingleItem input,
.dialog__rowSingleItem select {
  padding: 7px 10px;
  border-radius: 10px;
  border: 2px solid lightgray;
  outline: none;
}
.dialog__row > input:focus,
.dialog__rowSingleItem > input:focus,
.dialog__row > select:focus {
  border: 2px solid dodgerblue;
}

.form__Buttons {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  margin-top: 15px;
}

.form__Buttons > button {
  padding: 7px 20px;
  margin: 0px 20px;
  max-width: 150px;
  color: white;
  border-radius: 20px;
  border: none;
  outline: none;
  cursor: pointer;
}
.form__Buttons > button:nth-child(1) {
  background: linear-gradient(to bottom, #cd03ff, #f00030);
  justify-self: start;
}
.form__Buttons > button:nth-child(2) {
  background: linear-gradient(to right, #4723d9, dodgerblue);
  background: linear-gradient(to right, var(--first-color), dodgerblue);
  justify-self: end;
}
.extra__formButtons {
  display: flex;
  justify-content: space-between;
  width: 95%;
  margin: 15px auto;
  color: dodgerblue;
  font-weight: bold;
}
/* COMMON TABLE STYLING */
.table__parent {
  max-width: 100%;
  margin: 10vh auto 20px auto;
}
.table__parentHeader {
  text-align: center;
}
.table__parentHeader > h3 {
  color: dodgerblue;
  text-decoration: underline;
  margin-top: 10px;
}
.table__parentSubHeader {
  display: flex;
  flex-wrap: wrap;
  max-width: 600px;
  margin: 8px auto 5px auto;
  justify-content: space-around;
  align-items: flex-end;
}
.table__parentSubHeader > span {
  display: flex;
  flex-direction: column;
}
.table__parentSubHeader > * {
  margin-bottom: 8px;
  margin-right: 8px;
}
.table__listing {
  width: 100%;
  max-width: 1200px;
  margin: 5px auto 25px auto;
}
.table__listingHeader {
  color: white;
  background: linear-gradient(to bottom, #4723d9, dodgerblue);
  background: linear-gradient(to bottom, var(--first-color), dodgerblue);
}
.table__listingHeader > th {
  text-align: left;
  padding: 6px;
}
.table__listingItem {
  background-color: #f1f1f1;
}
.table__listingItem > td {
  padding: 5px;
  text-align: left;
}
/* showing tables totals */
.table__listingFooter > th {
  padding: 5px 10px;
  text-align: left;
  background: linear-gradient(to right, #ebeded, #d7dbda);
  color: dodgerblue;
}
/* end of dialog components */

#pageSubmitting,
#formSubmitting {
  opacity: 0.3;
  pointer-events: none;
  cursor: not-allowed !important;
}

/* error styling */
.response__message {
  font-weight: bold;
  text-align: center;
  height: -webkit-fit-content;
  height: fit-content;
  font-size: 1rem;
  margin-top: 5px auto;
  padding: 4px;
  width: 90%;
  position: absolute;
  max-width: 100%;
}
/* we set the background for each item since we only want it to be visible when the error is being shown */
.error {
  color: red;
  background-color: #f1f1f1;
}
.success {
  color: #02ab4b;
  background-color: #f1f1f1;
}
.not__available {
  color: red !important;
  text-align: center !important;
}
.remove__item:hover {
  cursor: pointer;
  text-decoration: line-through;
  color: red;
}
/* ===== MEDIA QUERIES=====*/
@media screen and (min-width: 768px) {
  body {
    margin: calc(3rem + 1rem) 0 0 0;
    margin: calc(var(--header-height) + 1rem) 0 0 0;
    /* padding-left: calc(var(--nav-width) + 2rem); */
  }

  /*Add padding body desktop*/
  .body-pd {
    /* margin-left: calc(var(--nav-width)); */
  }
}

/*===== HEADER =====*/
.header {
  width: 100%;
  height: var(--header-height);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
  background: linear-gradient(to bottom, var(--first-color), dodgerblue);
  z-index: var(--z-fixed);
  transition: 0.5s;
}

.header__toggle {
  color: var(--white-color);
  font-size: 1.5rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__toggle > i {
  padding-top: 5px;
}
.header__toggle > h1 {
  font-size: 1.5rem;
  padding-left: 1.2rem;
}
.header__right {
  flex: 0.1 1;
  display: flex;
  color: var(--white-color);
  max-height: 100%;
  font-weight: 700;
  justify-content: space-between;
}
.authenticated {
  flex-direction: column;
}
.authenticated > div {
  display: flex;
  align-items: center;
  z-index: 1;
  cursor: pointer;
}
.authenticated > div > .user__image {
  height: 35px;
  width: 35px;
  margin-right: 5px;
}
.authenticated > div > h6 {
  font-size: 1rem;
}
.authenticated > div > i {
  margin-left: 5px;
}
.authenticated > .dropdown {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  position: absolute;
  background-color: dodgerblue;
  padding: 45px 15px 5px 15px;
  max-width: 120px;
  text-align: center;
  z-index: 0;
  display: none;
}
.authenticated:hover .dropdown {
  display: block;
}

.authenticated > .dropdown > li {
  cursor: pointer;
  padding: 2px 5px;
}
.authenticated > .dropdown > li:hover {
  color: yellow;
  opacity: 0.7;
}

/* ===== MEDIA QUERIES=====*/
@media screen and (min-width: 768px) {
  .header {
    height: calc(var(--header-height) + 1rem);
    padding: 0 2rem 0 2rem;
  }
}

.search__input {
  padding: 5px;
  border-radius: 15px;
  border: 2px solid grey;
  outline: none;
}
.search__input:focus {
  border: 2px solid dodgerblue;
}

.search__button {
  margin-top: 10px;
}
.map__iframe {
  border: 0;
  max-width: 100%;
  max-height: 100%;
}

/*===== NAV =====*/
.left-navbar {
  position: fixed;
  top: 0;
  left: -15px !important;
  width: 0px;
  height: 100vh;
  background: linear-gradient(to right, var(--first-color), dodgerblue);
  padding: 0.5rem 1rem 0 0;
  transition: 0.5s;
  /* z-index: var(--z-fixed); */
}

.nav {
  /* height: 100%; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}

.nav__logo,
.nav__link {
  display: grid;
  grid-template-columns: -webkit-max-content -webkit-max-content;
  grid-template-columns: max-content max-content;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  padding: 0.5rem 0 0.5rem 1.5rem;
}

.nav__logo {
  margin-bottom: 2rem;
}

.nav__logo-icon {
  font-size: 1.25rem;
  color: var(--white-color);
}

.nav__logo-name {
  color: var(--white-color);
  font-weight: 700;
}

.nav__link {
  position: relative;
  color: var(--first-color-light);
  margin-bottom: 1.5rem;
  transition: 0.3s;
}

.nav__link:hover {
  color: var(--white-color);
}

.nav__icon {
  font-size: 1.25rem;
}

/*Show navbar movil*/
.show {
  left: 0;
}

/*Add padding body movil*/
.body-pd {
  padding-left: calc(var(--nav-width) + 1rem);
}

/*Active links*/
.active {
  color: var(--white-color);
}

.active::before {
  content: "";
  position: absolute;
  left: 0;
  width: 2px;
  height: 32px;
  background-color: var(--white-color);
}

/* ===== MEDIA QUERIES=====*/
